.home {
  display: flex;
  background-color: #eee;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  color: white;
  flex-direction: column;
  width: 100%;
}

.banner {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: relative;
  max-height: 30%;
  padding-left: 200px;
  padding-right: 200px;
  text-align: center;
  background-image: url('../../assets/servers-different.jpg');
  background-size: cover;
}

.text {
  margin-top: 150px;
}

.linha {
  display: flex;
  align-items: center;
  justify-content: center;
}
.linha > img {
  margin-right: 35px;
}

.text > .linha > h1 {
  font-family: 'Poppins', sans-serif;
  color: white;
  font-size: 50px;
  font-weight: bold;
}
.text > p {
  padding-top: 40px;
  font-weight: 300;
  font-size: 35px;
  margin-bottom: 50px;
}

.cards {
  max-width: 2160px;
  margin-top: 100px;
  display: flex;
  flex-direction: row;
  margin-left: 200px;
  margin-right: 200px;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.cards > .card {
  background: white;
  width: 390px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cards > .card > img {
  margin-top: 15px;
  margin-bottom: 15px;
  fill: #333;
}
.cards > .card > .phrases > h2 {
  font-size: 30px;
  font-weight: bold;
  color: #333;
  text-align: center;
}
.cards > .card > .phrases > p {
  font-weight: 400;
  color: #333;
  font-size: 15px;
  margin-bottom: 25px;
  margin-top: 25px;
  text-align: center;
}

.cards > .card > .phrases {
  margin-left: 15px;
  margin-right: 15px;
}

.cards > .card > .button {
  background-color: #333;
  color: white;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 15px;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.offers {
  max-width: 2160px;
}

.offers > .textos {
  margin-top: 200px;
}

.offers > .textos > h2 {
  font-size: 50px;
  font-weight: 600;
  color: black;
  text-align: center;
}

.offers > .textos > h2 > span {
  font-weight: 200;
}

.picker {
  max-width: 2160px;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  color: #333;
  font-size: 25px;
  justify-content: space-around;
  max-width: 50%;
  margin-left: auto;
  margin-right: auto;
  align-content: space-around;
  margin-top: 40px;
  font-weight: 600;
}
.secondpicker {
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  color: #333;
  font-size: 25px;
  justify-content: space-around;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  align-content: space-around;
  margin-top: 40px;
  font-weight: 600;
}
.secondpicker > p {
  cursor: pointer;
}
.secondpicker .active {
  color: #8f0b96;
}

.picker > p {
  cursor: pointer;
}

.picker .active {
  color: #8f0b96;
}
.description {
  max-width: 2160px;
  display: flex;
  justify-content: space-between;
  margin-top: 150px;
  align-items: center;
  zoom: 0.8;
}

.description2 {
  display: flex;
  justify-content: center;
  margin-top: 150px;
  align-items: center;
  zoom: 0.8;
}

.explicacao {
  flex: 1;
  max-width: 900px;
}
.explicacao2 {
  max-width: 1000px;
  display: flex;
}
.explicacao2 > h2 {
  font-size: 48px;
  font-weight: 400;
  color: #333;
  text-align: center;
}
.explicacao2 > h2 > bold {
  font-weight: 700;
}
.explicacao > h2 {
  font-size: 48px;
  font-weight: 400;
  color: #333;
  text-align: left;
}
.explicacao > h2 > bold {
  font-weight: 700;
}

.img {
  border-radius: 20px;
}

.footer {
  background-color: #6d37a7;
  width: 100%;
  margin-top: 200px;
  height: 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.footer > h1 {
  margin-left: 200px;
  font-size: 50px;
  font-weight: bold;
  color: #fff;
}
.footer > nav {
  display: flex;
  color: white;
  margin-left: auto;
  margin-right: 200px;
  font-weight: 400;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
}
nav > a:first-of-type {
  margin-right: 50px;
}
